<script>
export default {
  data: () => ({
    cur: 0,
  }),
  computed: {
    numbs() {
      return Math.round((100 / 3) * this.cur);
    },
    widths() {
      return `width: ${Math.round((100 / 3) * this.cur)}%`;
    },
  },
  methods: {
    cl() {
      if (this.cur + 1 === 3) {
        this.cur = 0;
      } else {
        this.cur += 1;
      }
    },
  },
};
